"use client"

import { DashboardHeader } from "@/components/dashboard-header"
import IdCard from "@/components/id-card"
import { CategoryButtons } from "@/components/category-buttons"
import { SkipToMain } from "@/components/skip-to-main"
import { VoiceAssistant } from "@/components/voice-assistant"
import { useState } from "react"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { ArrowLeft, User, AlertTriangle, Phone } from "lucide-react"

export default function DashboardPage() {
  const [icLostOpen, setIcLostOpen] = useState(false)
  const [policeOpen, setPoliceOpen] = useState(false)
  const [reportSubmitted, setReportSubmitted] = useState(false)

  const user = {
    id: "demo-user",
    email: "demo@mykita.gov.my",
  }

  const profile = {
    full_name: "Ahmad Abdullah",
    ic_number: "900101-01-1234",
  }

    const handleICLostReport = () => {
    setReportSubmitted(true)
    setTimeout(() => {
      setIcLostOpen(false)
      setReportSubmitted(false)
      alert("IC Lost Report submitted successfully. Reference: IC-2024-0001")
    }, 1500)
  }

  const handlePoliceReport = () => {
    setReportSubmitted(true)
    setTimeout(() => {
      setPoliceOpen(false)
      setReportSubmitted(false)
      alert("Police Report submitted successfully. Reference: POL-2024-0001")
    }, 1500)
  }

  return (
    <div className="min-h-screen bg-background">
      <SkipToMain />
      <DashboardHeader userName={profile?.full_name || user.email || "User"} userId={user.id} />
      
        <main id="main-content" className="container mx-auto px-4 py-8 md:px-6 md:py-12">   
        <div className="mb-8 space-y-2">
          <h1 className="text-5xl font-bold tracking-tight text-balance md:text-6xl">MyKita Services</h1>
          <p className="text-2xl text-muted-foreground leading-relaxed">
            Select a category to access your government services
          </p>
        </div>
        <CategoryButtons />
        <div className="mb-6 grid gap-4 sm:grid-cols-2">
          <Dialog open={icLostOpen} onOpenChange={setIcLostOpen}>
            <DialogTrigger asChild>
              <Button variant="destructive" size="lg" className="h-20 text-xl gap-3">
                <AlertTriangle className="h-8 w-8" />
                Report IC Lost
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[500px]">
              <DialogHeader>
                <DialogTitle className="text-2xl">Report IC Lost</DialogTitle>
                <DialogDescription className="text-lg">
                  Are you sure you want to report your identity card as lost? This will:
                </DialogDescription>
              </DialogHeader>
              <ul className="list-disc list-inside space-y-2 text-base py-4">
                <li>Block your current IC from being used</li>
                <li>Notify relevant government agencies</li>
                <li>Generate a police report reference number</li>
                <li>Allow you to apply for a replacement IC</li>
              </ul>
              <DialogFooter className="gap-2">
                <Button
                  variant="outline"
                  onClick={() => setIcLostOpen(false)}
                  className="h-14 text-lg"
                  disabled={reportSubmitted}
                >
                  Cancel
                </Button>
                <Button
                  variant="destructive"
                  onClick={handleICLostReport}
                  className="h-14 text-lg"
                  disabled={reportSubmitted}
                >
                  {reportSubmitted ? "Submitting..." : "Confirm Report"}
                </Button>
              </DialogFooter>
            </DialogContent>
          </Dialog>

          <Dialog open={policeOpen} onOpenChange={setPoliceOpen}>
            <DialogTrigger asChild>
              <Button variant="default" size="lg" className="h-20 text-xl gap-3">
                <Phone className="h-8 w-8" />
                Report Police
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[500px]">
              <DialogHeader>
                <DialogTitle className="text-2xl">Make Police Report</DialogTitle>
                <DialogDescription className="text-lg">
                  Submit an online police report for non-emergency incidents.
                </DialogDescription>
              </DialogHeader>
              <div className="py-4 space-y-4">
                <p className="text-base">This will create an online police report for incidents such as:</p>
                <ul className="list-disc list-inside space-y-2 text-base">
                  <li>Lost items or documents</li>
                  <li>Minor theft</li>
                  <li>Property damage</li>
                  <li>Other non-emergency matters</li>
                </ul>
                <div className="bg-muted p-4 rounded-lg">
                  <p className="text-base font-semibold">For emergencies, call 999</p>
                </div>
              </div>
              <DialogFooter className="gap-2">
                <Button
                  variant="outline"
                  onClick={() => setPoliceOpen(false)}
                  className="h-14 text-lg"
                  disabled={reportSubmitted}
                >
                  Cancel
                </Button>
                <Button onClick={handlePoliceReport} className="h-14 text-lg" disabled={reportSubmitted}>
                  {reportSubmitted ? "Submitting..." : "Submit Report"}
                </Button>
              </DialogFooter>
            </DialogContent>
          </Dialog>
        </div>
      </main>
      <VoiceAssistant />
    </div>
  )
}
